/* TOP MENU BAR */

.topmenu {
    width: 100%;
    height: fit-content;

    padding-bottom: 5px;

    background-color: rgb(50, 50, 50);
}

/* CURRENT TAB */

.topmenu-curtab {
    width: fit-content;

    padding-right: 10px;
    padding-left: 5px;

    color: rgb(255, 255, 255);
    font-weight: 300;
    font-size: 24px;
}

.topmenu-curtab span {
    position: relative;

    transition: 0.5s;
}

.topmenu-curtab span:after {
    position: absolute;

    left: 0px;

    opacity: 0;

    content: '\00bb';
    font-weight: 500;
    font-size: 24px;

    transition: 0.5s;
}

.topmenu-curtab:hover span {
    padding-left: 15px;
}

.topmenu-curtab:hover span:after {
    opacity: 1;
}

.topmenu-curtab:hover .topmenu-tablist {
    display: inline;
}

/* TOP MENU BAR TAB LIST */

.topmenu-tablist {
    display: none;

    position: absolute;
}

.topmenu-tablist-animate {
    animation: growRight 500ms ease-in-out forwards;
    
    transform-origin: left center;
}

@keyframes growRight {
  0% {
    transform: scaleX(0);
  }
  
  100% {
    transform: scaleX(1);
  }
}

/* TOP MENU BAR TAB SELECTION */

.topmenu-tab {
    display: inline-flex;

    margin-left: 30px;
    padding-right: 10px;
    padding-left: 10px;

    background-color: transparent;
    cursor: pointer;
    opacity: 0.5;

    color: rgb(255, 255, 255);
    font-size: 24px;
    font-weight: 300;
}

.topmenu-tab:hover {
    opacity: 1;

    transition: 0.5s;
}

/* TAB COMPONENT CONTENT */

.tab-content {
    display: none;
}